import { Component, Prop, State, Event, EventEmitter, h, Host, Watch } from '@stencil/core';

export interface ChildItem {
  label: string;
  badge?: string | number;
  showBadge?: boolean;
  isActive?: boolean;
}

@Component({
  tag: 'bh-page-navigation-multi-level',
  styleUrl: 'bh-page-navigation-multi-level.css',
  shadow: true,
})
export class BhPageNavigationMultiLevel {
  /**
   * Label text for the navigation item
   */
  @Prop() label!: string;

  /**
   * Material icon name
   */
  @Prop() icon: string = 'radio_button_unchecked';

  /**
   * Optional badge text/number to display
   */
  @Prop() badge?: string | number;

  /**
   * Child navigation items
   */
  @Prop() childItems: ChildItem[] = [];

  /**
   * Whether the item is expanded to show children
   */
  @Prop({ mutable: true }) isExpanded: boolean = false;

  /**
   * Internal state for hover
   */
  @State() isHovered: boolean = false;

  /**
   * Internal state for animation
   */
  @State() isAnimating: boolean = false;

  /**
   * Animation class
   */
  @State() animationClass: string = '';

  /**
   * Event emitted when the parent item is clicked (to toggle expansion)
   */
  @Event() bhToggle!: EventEmitter<void>;

  /**
   * Event emitted when a child item is clicked
   */
  @Event() bhChildClick!: EventEmitter<number>;

  /**
   * Event emitted when add button is clicked
   */
  @Event() bhAddClick!: EventEmitter<void>;

  @Watch('isExpanded')
  handleExpandedChange(newValue: boolean) {
    if (newValue) {
      this.isAnimating = true;
      this.animationClass = 'animate-slide-down-enter';
    } else if (this.isAnimating) {
      this.animationClass = 'animate-slide-down-exit';
      setTimeout(() => {
        this.isAnimating = false;
        this.animationClass = '';
      }, 150);
    }
  }

  private get hasActiveChild(): boolean {
    return this.childItems.some(child => child.isActive);
  }

  private handleParentClick = () => {
    this.bhToggle.emit();
  };

  private handleChildClick = (index: number) => {
    this.bhChildClick.emit(index);
  };

  private handleAddClick = (e: MouseEvent) => {
    e.stopPropagation();
    this.bhAddClick.emit();
  };

  private getItemClasses() {
    return {
      'page-navigation-multi-level': true,
      'page-navigation-animate-smooth': true,
      'has-active-child': this.hasActiveChild,
      'expanded': this.isExpanded,
    };
  }

  render() {
    return (
      <Host>
        <div class={this.getItemClasses()}>
          <div
            class="page-navigation-multi-level-parent"
            onClick={this.handleParentClick}
            onMouseEnter={() => (this.isHovered = true)}
            onMouseLeave={() => (this.isHovered = false)}
          >
            <div class="page-navigation-item-content">
              {!this.isHovered && (
                <span class="page-navigation-item-icon material-symbols-outlined">
                  {this.icon}
                </span>
              )}
              {this.isHovered && (
                <span
                  class={{
                    'page-navigation-chevron-icon': true,
                    'material-symbols-outlined': true,
                    'expanded': this.isExpanded,
                  }}
                >
                  expand_more
                </span>
              )}
              <span class="page-navigation-item-label">{this.label}</span>
              <bh-button
                hierarchy="tertiary"
                icon="only"
                size="xs"
                iconName="add"
                onClick={this.handleAddClick}
              ></bh-button>
            </div>
          </div>

          {(this.isExpanded || this.isAnimating) && (
            <div class={`page-navigation-children ${this.animationClass}`}>
              {this.childItems.map((child, index) => (
                <bh-page-navigation-child
                  key={index}
                  label={child.label}
                  badge={child.badge}
                  showBadge={child.showBadge}
                  isActive={child.isActive}
                  onBhItemClick={() => this.handleChildClick(index)}
                ></bh-page-navigation-child>
              ))}
            </div>
          )}
        </div>
      </Host>
    );
  }
}
